<template>
  <div class="modal-document">
    <h4 class="fw-bold">„Rosyjski walec” na polskich Kresach - Piotr Korczyński</h4>
    <p class="fw-bold">
      „Cios w plecy!” – tak brytyjski „Times” dzień po sowieckiej agresji na Polskę zatytułował swą pierwszą szpaltę.
      Tytuł trafnie oddawał działania Armii Czerwonej, która wypełniała sojusznicze zobowiązania wobec Wehrmachtu, ale
      jednocześnie wykorzystywała bezczynność zachodnich sojuszników Polski, którzy oddali ją na pastwę dwóch
      totalitarnych sąsiadów.
    </p>
    <p>
      Sobota 16 września 1939 roku była czarnym dniem dla oddziałów SS walczących w Polsce. Gwardia Hitlera została
      mocno
      przetrzebiona przez polskich żołnierzy. Najpierw w lasach jaworowskich oddziały gen. Kazimierza Sosnkowskiego
      idące
      na odsiecz Lwowa rozgromiły Pułk Zmotoryzowany SS „Germania”, a następnie pod Sochaczewem jedna
      bateria 17 Pułku
      Artylerii Lekkiej zatrzymała natarcie całej 4 Dywizji Pancernej, w tym elitarnego Pułku „Leibstandarte SS Adolf
      Hitler”. Kilka polskich dział uszkodziło i zniszczyło 22 niemieckie czołgi i samochody pancerne! Te wiadomości
      wzbudziły w kwaterze głównej Führera wściekłość. Hitler miał prawo być zdenerwowany – mimo zapewnień swych
      dowódców,
      że w kilka dni zdobędą Warszawę, stolica Polski wciąż się broniła, a Wojsko Polskie nadal było zdolne zadawać
      bolesne ciosy. Na domiar złego, Stalin zwlekał z wypełnieniem umowy zawartej w pakcie Ribbentrop-Mołotow i nie
      atakował od wschodu Rzeczypospolitej, mimo coraz natarczywszych nawoływań z Berlina.
    </p>

    <h4 class="fw-bold">Kremlowskie kuranty</h4>
    <p>
      Stalin opóźniał atak na Polskę, gdyż mimo sojuszu z Hitlerem, wciąż oglądał się na Zachód. Wiedział, że Armia
      Czerwona po czystce jej dowództwa, którą sam nakazał, nie jest jeszcze gotowa do walki z wojskami alianckimi.
      Jednakże tego samego dnia, w którym polscy żołnierze gromili pułki SS, francuscy i angielscy sztabowcy na
      konferencji w Abbeville zdecydowali, że nie ruszą zbrojnie Polakom na pomoc, o czym sowieccy agenci skwapliwie
      donieśli władzom na Kremlu. Po otrzymaniu tej wiadomości Stalin nie miał już żadnych oporów i rozkazał słać wici
      do
      wojsk, które skoncentrował na swej zachodniej granicy.
    </p>
    <p>
      Nad ranem 17 września Sowieci wezwali ambasadora Rzeczypospolitej w Moskwie Wacława Grzybowskiego i wręczyli mu
      notę, w której stwierdzono, że „państwo polskie i jego Rząd przestały faktycznie istnieć. Dlatego też straciły
      ważność traktaty zawarte pomiędzy ZSRR a Polską. […] Rząd Radziecki nie może również pozostać obojętny na fakt, że
      zamieszkująca terytorium Polski pokrewna ludność ukraińskiego i białoruskiego pochodzenia jest bezbronna i została
      pozostawiona własnemu losowi. Rząd Radziecki polecił wobec powyższych okoliczności Naczelnemu Dowództwu Armii
      Czerwonej, aby nakazało wojskom przekroczyć granicę i wziąć pod swą opiekę życie i mienie ludności Zachodniej
      Ukrainy i Zachodniej Białorusi”. Stwierdzenie, że państwo polskie nie istnieje miało zabezpieczyć Związek Sowiecki
      na wypadek reakcji mocarstw zachodnich i stworzyć pozory neutralności w konflikcie między Niemcami a Francją i
      Wielką Brytanią, co w pełni się udało.
    </p>
    <p>
      Więcej nawet, gdyż mimo że ambasador Grzybowski nie przyjął kłamliwej noty, polskie władze, pod naciskiem
      zachodnich
      sojuszników, zaniechały wydania oświadczenia, że są w stanie wojny ze Związkiem Sowieckim. Zachód wciąż liczył, że
      przeciągnie Sowietów na swoją stronę. Natomiast polskie władze cywilne i wojskowe były zaskoczone sowieckim
      atakiem,
      choć wywiad od dawna informował o koncentracji wojsk sowieckich na granicy. Naczelny Wódz, marszałek Edward
      Rydz-Śmigły dopiero wieczorem 17 września wydał dyrektywę, by Warszawa i Modlin stawiały opór aż do wyczerpania
      amunicji, a wszystkie oddziały wycofywały się na Węgry lub do Rumunii. Na walkę z Sowietami zezwalał tylko
      wówczas,
      gdyby atakowali oni polskie wojska lub próbowali je rozbrajać. To ostatnie stwierdzenie wprowadziło jedynie chaos,
      gdyż polscy dowódcy różnie je interpretowali i nie byli pewni, jak mają się zachować. Tym bardziej że często
      Sowieci
      w celach dywersyjnych głosili, że idą Polsce z pomocą.
    </p>
    <p>
      W nocy z 17 na 18 września prezydent oraz rząd Rzeczypospolitej udali się do Rumunii. Podobnie uczynił, wraz ze
      swym
      sztabem, marszałek Rydz-Śmigły, co w znacznej części polskiego społeczeństwa wywołało oburzenie i spowodowało
      oskarżenia o porzucenie walczącego wciąż wojska.
    </p>

    <h4 class="fw-bold">Boje z sowieckim kolosem</h4>

    <p>
      Nacierające na Polskę wojska sowieckie zostały uszykowane w dwa fronty. Front Białoruski komandarma Michaiła
      Kowalowa w pierwszych godzinach ataku liczył 15 dywizji piechoty i kawalerii oraz siedem brygad pancernych (ponad
      2000 czołgów i samochodów pancernych) – w sumie ponad 200 tys. żołnierzy i liczba ta w kolejnych dniach jeszcze
      się
      zwiększyła. Natomiast Front Ukraiński komandarma Siemiona Timoszenki miał w pierwszym rzucie 16 dywizji piechoty i
      kawalerii oraz osiem brygad pancernych (blisko 3000 czołgów i samochodów pancernych), czyli ponad 265 tys.
      żołnierzy, wspieranych przez flotę powietrzną liczącą około tysiąca samolotów. Ponadto regularne oddziały Armii
      Czerwonej wsparły grupy dywersyjne, tworzone i uzbrajane przez sowiecki wywiad wojskowy i NKWD z białoruskich,
      ukraińskich i żydowskich obywateli Rzeczypospolitej.
    </p>
    <p>
      Tej nawale na Kresach Wschodnich Polacy mogli przeciwstawić niewspółmiernie słabsze siły. Dowództwo polskie
      pozostawiło do ochrony tych ziem około 400 tys. żołnierzy. Granicy bezpośrednio strzegło blisko 12 tys. żołnierzy
      Korpusu Ochrony Pogranicza, zorganizowanych w 25 batalionach i siedmiu szwadronach kawalerii. Większość tych
      oddziałów, mimo przytłaczającej przewagi nieprzyjaciela, podjęła z nim walkę. Zacięte boje trwały na
      Wileńszczyźnie,
      Nowogródczyźnie i Podolu. Przez trzy dni sowiecką nawałę powstrzymywał batalion forteczny KOP „Sarny” na
      umocnionej
      pozycji pod Słuczem. 20 i 21 września doszło do walk ulicznych w Grodnie, w czasie których znaczne straty poniosły
      sowieckie oddziały pancerne. 22 września pod Kodziowcami 101 Pułk Ułanów z powodzeniem zatrzymał natarcie
      Sowietów,
      niszcząc im wiele czołgów.
    </p>
    <p>
      Najwięcej starć z Armią Czerwoną miała na koncie wycofująca się od granicy na Polesie grupa KOP gen. Wilhelma
      Orlika-Rückemana. Jej największym sukcesem było rozgromienie 28 września pod Szackiem sztabu i części oddziałów 52
      Dywizji Strzeleckiej. Równie skutecznie walczył w rejonie Borowicz, Nawozu i Hruziatyna 3 Pułk Piechoty KOP płk.
      Zdzisława Zajączkowskiego. Także oddziały Samodzielnej Grupy Operacyjnej „Polesie” gen. Franciszka Kleeberga
      ścierały się niejednokrotnie z Armią Czerwoną, m.in. pod Jabłonią i Milanowem.
      Jednak polskie wojska nie miały szans, by zatrzymać sowieckiego kolosa. Mimo że kampania polska wyraźnie pokazała,
      że Armią Czerwoną dowodzą przeważnie niekompetentni dowódcy, a żołnierze są słabo wyszkoleni i wyposażeni, to
      jednak
      samą swą masą – osławionym „rosyjskim walcem” – dosłownie zalali polskie Kresy. Sowieci również bardzo szybko
      przestali udawać, że nie współpracują z Niemcami, czego wymownym symbolem była m.in. wspólna defilada wojskowa w
      Brześciu nad Bugiem. „Prawda” od 23 września zaczęła publikować mapki z linią rozgraniczającą niemieckie i
      sowieckie
      zdobycze, dowodząc dobitnie, że III Rzesza i Związek Sowiecki dokonują właśnie czwartego rozbioru Polski.
    </p>

